import React, { useState } from "react";
import srcicon from "./assets/search-icon.svg";
import hamBurger from "./assets/hamburger.svg";
import Classes from "./App.module.css";
import styled from 'styled-components';
import {
  Container,
  Header,
  SearchIcon,
  SearchInput,
} from "./components/headerComponent";

import {
  RecipeListContainer
} from "./components/RecipeComponent";

import axios from "axios";
import Recipes from './components/Recipes';

const Placeholder = styled.img`
    height: 100px;
    opacity: 50%;
`;

const APP_ID = "53a0754c";
const APP_KEY = "ea9f07283744e58ecac5b76473b091d7";

function App() {
  const [fetchedRecipes, setfetchedRecipes] = useState([]);
  const fetchRecipe = async (searchString) => {
    
        const response =  await axios.get(
            `https://api.edamam.com/search?q=${searchString}&app_id=${APP_ID}&app_key=${APP_KEY}`
        );
        setfetchedRecipes(response.data.hits);
  };
  //Debouncing, keep delete old setTimeouts which have not completed their time span
  //If you haven't press any key for 500ms then old/last setTimeout will not be
  //deleted and it will execute
  const [timeOutId, setTimeoutId] = useState();
  const onTextChange = (event) => {
    clearTimeout(timeOutId);
    let val = event.target.value;
    const timeOut = setTimeout(() => fetchRecipe(val), 500);
    setTimeoutId(timeOut);
  };

  return (
    <Container>
      <Header>
        <div className={Classes.appNameComponent}>
          <img className={Classes.appIcon} src={hamBurger}></img>
          Reciepe Finder
        </div>
        <div className={Classes.searchComponent}>
          <SearchIcon src={srcicon}></SearchIcon>
          <SearchInput
            type="text"
            onChange={onTextChange}
            placeholder="Search Recipe"
          />
        </div>
      </Header>
      <RecipeListContainer>
        {fetchedRecipes.length ? fetchedRecipes.map((val, ind) => {
          return <Recipes key={ind} RecipieObj = {val.recipe}/>;
        }) : <Placeholder src={hamBurger}/>}
      </RecipeListContainer>
    </Container> 
  );
}

export default App;
