import React from "react";
import BackDrop from "./Backdrop";
import Classes from "./Modal.module.css";
import Aux from "./hoc/_ux";

const Modal = ({ showIngredient, show, ing, lab, url }) => {
console.log(url);
  return (
    <Aux>
      <BackDrop show={show} showIngredient={showIngredient} />
      <div
        className={Classes.modal}
        style={{
          transform: show ? "translateY(0)" : "translateY(-100vh)",
        }}
      >
        <span>Ingredients</span>
        <h3>{lab}</h3>
        <table>
          <thead>
            <tr>
              <th>Ingredient</th>
              <th style={{textAlign: 'left'}}>Weight</th>
            </tr>
          </thead>
          <tbody>
            {ing.map((val) => {
              return (
                <tr key={val.foodId}>
                  <td>{val.text}</td>
                  <td>{val.weight}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
        <button onClick={() => window.open(url)}>Show More</button>
        <button onClick={showIngredient}>Close</button>
      </div>
    </Aux>
  );
};

export default Modal;
