import React, { useState } from "react";
import {
  RecipeContainer,
  CoverImage,
  RecipeName,
  IngredientsText,
  SeeMoreText,
} from "./RecipeComponent";
import Modal from "./Modal";
import Aux from "./hoc/_ux";

const Recipes = ({ RecipieObj: { image, label, url, ingredients } }) => {
  const [showIng, updateShowIng] = useState(false);

  const showIngredients = (props) => {
    updateShowIng((prev) => !prev);
  };

  return (
    <Aux>
      <Modal show={showIng} showIngredient={showIngredients} ing={ingredients} lab={label} url={url}/>
      <RecipeContainer>
        <CoverImage src={image}></CoverImage>
        <RecipeName> {label}</RecipeName>
        <IngredientsText onClick={showIngredients}>Ingredients</IngredientsText>
        <SeeMoreText onClick={() => window.open(url)}>
          see complete recipe
        </SeeMoreText>
      </RecipeContainer>
    </Aux>
  );
};

export default Recipes;
